.about-section {
  background-color: #ffffff; /* Fondo suave para la sección */
  margin-top: 50px;
}
.section_cards {
  background-color: #f9f9f9;
 
}

.about-section h2 {
  font-size: 2.5rem!important;
  font-weight: 900;
  color: #2c6f2f; /* Color verde de Ecoplan */
  margin-bottom: 1rem;
}
.section-title{
padding-top: 50px;
}
.about-section p {
  font-size: 1.125rem;
  color: #555;
  line-height: 1.6;
  padding: 20px 20px 20px 20px;
}

.about-section img {
  height: 500px;            
  width: 650px;               
  object-fit: cover;     
  object-position: center; 
  
}
.card-body a{
  background-color: #2c6f2f;
  border-radius: 50px 50px 50px 50px;
}
.card-body a:hover{
  background-color: #1a8d20;
}

/* Resaltar en pantallas pequeñas */
@media (max-width: 767px) {
  .about-section h2 {
    font-size: 2rem;
  }

  .about-section p {
    font-size: 1rem;
  }
}
.capsula{
  height: 215px;
  background-color: #3c633e;
  color: #ffffff;
}
.capsula1 {
  height: 215px;
  background-color: #ffffff;
  border: 1px solid #3c633e;
  color: #3c633e!important;
}


.blog-section {
  background-color: #f9f9f9;
  padding-top: 50px;
}

.section-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1b5e20;
}

.section-description {
  font-size: 1.125rem;
  color: #555;
  max-width: 600px;
  margin: 0 auto 2rem;
}



.card-img-top {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 1px solid #ddd;
}



.card-body {
  padding: 35px 35px 35px 35px!important;
  flex-grow: 1; 
}



.card-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #1b5e20!important;
}

.text-primary{
  color: #1b5e20!important;
}

.card-text {
  font-size: 0.95rem;
  color: #666;
}

.flip-card {
  perspective: 1500px;
}


.flip-card .card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotateY(1deg);
  transition: transform 0.9s;
}


.flip-card:hover .card-inner {
  transform: rotateX(10deg);
}










